.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 136px;
  margin-bottom: 20px;
  border-bottom: 1px solid #f8f8f8;
  background: rgb(51, 204, 153);
  background: radial-gradient(
    circle,
    rgba(51, 204, 153, 0.5) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}

.logo {
  position: relative;
  text-align: center;
  margin: 40px;
  padding-bottom: 8px;
  font-size: 40px;
  color: #336699;
  text-transform: uppercase;
  text-decoration: none;
  transition: ease-in-out 0.3s;
  &::before {
    position: absolute;
    content: "";
    width: 60px;
    height: 60px;
    left: 155px;
    top: -15px;
    background-image: url("./icons/logo.svg");
    background-size: contain;
  }
  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 4px;
    left: 0;
    top: 48px;
    background-color: #336699;
  }
  & span {
    color: #33cc99;
    padding-left: 60px;
  }
  &:hover {
    transform: scale(1.1);
  }
}

.header_search {
  position: relative;
  width: 34%;
  background-color: #f8f8f8;
  padding: 10px 15px;
  border: none;
  border-radius: 10px;
  opacity: 0.75;
  transition: ease-in-out 0.2s;
  &:hover,
  &:focus-within {
    opacity: 1;
  }
  &::before {
    position: absolute;
    content: "search";
    top: 4px;
    left: 10px;
    font-family: "Material Symbols Outlined";
    font-size: 32px;
    font-weight: 400;
    color: #336699;
  }
}

.search-input {
  width: 100%;
  padding-left: 40px;
  font-family: "Russo One", sans-serif;
  color: #336699;
  background-color: transparent;
  font-weight: 100;
  font-size: 18px;
  border: none;
  outline: none;
  &::placeholder {
    color: #336699;
  }
}

.header_cart {
  display: flex;
  justify-content: center;
  padding: 0 20px;
  margin: 40px;
  color: #336699;
  background-color: rgb(51, 204, 153, 0.1);
  border-radius: 10px;
  cursor: pointer;
  transition: ease-in-out 0.2s;
  &:hover {
    background-color: rgb(51, 204, 153, 0.2);
    transform: scale(1.01);
  }
}

.cart-link {
  text-decoration: none;
}

.cart-count,
.cart-sum {
  display: inline-block;
  font-size: 16px;
}

.cart-count_title,
.cart-sum_title {
  position: relative;
  padding-left: 32px;
}

.cart-count_title::after,
.cart-sum_title::after {
  position: absolute;
  content: "shopping_cart";
  top: -8px;
  left: 0;
  font-family: "Material Symbols Outlined";
  font-size: 32px;
  color: #33cc99;
}

.cart-sum_title::after {
  content: "payments";
  left: -4px;
}

.cart-count_value,
.cart-sum_value {
  padding-left: 5px;
  color: #33cc99;
}

.cart-count_value {
  padding-right: 20px;
}

.display-none {
  display: none;
}

@media (max-width: 1280px) {
  .header {
    height: auto;
    flex-wrap: wrap;
  }

  .header_search {
    order: 1;
    margin: 0 auto 40px;
    width: 80%;
  }
}

@media (max-width: 760px) {
  .header {
    flex-direction: column;
    &_search {
      order: 0;
      margin: 0;
    }
    &_cart {
      margin: 20px;
    }
  }

  .logo {
    margin: 20px;
    font-size: 32px;
    & span {
      color: #33cc99;
      padding-left: 40px;
    }
    &::before {
      width: 40px;
      height: 40px;
      left: 124px;
      top: -5px;
    }
    &::after {
      height: 3px;
      left: 0;
      top: 40px;
    }
  }
}
